import {  useCallback } from 'react'
import styled from '@emotion/styled'
import { Button, Box } from '@material-ui/core'
import { withTranslation, WithTranslation, TFunction } from 'react-i18next'
import { withRouter, RouteComponentProps } from 'react-router-dom'
import { StarIcon } from 'static-resource'
import { Column, generateColumns, generateRows, Table } from '../../basic-lib/tables/index'
import { TablePaddingX } from '../../styled'

const TableStyled = styled(Box)`
    display: flex;
    flex-direction: column;
    flex: 1;

    .rdg{
        --template-columns: 240px auto auto auto auto auto 68px !important;
        .rdg-cell.action{
        display: flex;
        justify-content: center;
        align-items: center;
        }
    }
    ${({theme}) => TablePaddingX({pLeft:theme.unit * 3,pRight:theme.unit * 3})}
` as typeof Box

interface Row {
    price: string
    size: string
    volume: string
    number: string
    sortColumn: string
    filterColumn: string
    cellExpend: {
        value: string
        children: []
        isExpanded: boolean
    }
    children?: Row[]
    isExpanded?: boolean
    format?: any
}

// export type QuoteTableRawDataItem = (string | number | string[] | number[])[]
export type QuoteTableRawDataItem = {
    pair: {
        first: string;
        last: string;
    }
    lastPrice: number;
    change: number;
    high: number;
    low: number;
    volume: number;
}

const CoinPairCell: any = styled.span`
	display: flex;
	align-items: center;
`

const QuoteTableChangedCell: any = styled.span`
	color: ${(props: any) => {
        const {theme: {colorBase}} = props
        return props.value > 0
            ? colorBase.success
            : props.value < 0
                ? colorBase.error
                : colorBase.textSecondary
    }
}
`

const getColumnModelQuoteTable = (t: TFunction, history: any): Column<Row, unknown>[] => [
    {
        key: 'pair',
        name: t('labelQuotaPair'),
        sortable: true,
        formatter: ({row, column}) => {
            const {first, last} = row[ column.key ]
            const RenderValue = styled.span`
				color: ${({theme}) => theme.colorBase.textSecondary}
			`
            return (
                <div className="rdg-cell-value">
                    <CoinPairCell>
                        <StarIcon/>
                        <span style={{marginLeft: 8, marginTop: 3}}>
							{first}
                            <RenderValue> / {last}</RenderValue>
						</span>
                    </CoinPairCell>
                </div>
            )
        },
    },
    {
        key: 'lastPrice',
        name: t('labelQuotaLastPrice'),
        sortable: true,
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            // const [valueFirst, valueLast] = value
            const getRenderValue = (value: number) => Number.isFinite(value) ? value.toFixed(2) : '--'
            // const RenderValue = styled.span`
			// 	color: ${({theme}) => theme.colorBase.textSecondary}
			// `
            return (
                <div className="rdg-cell-value">
                    <span>{getRenderValue(value)}</span>
                </div>
            )
        },
    },
    {
        key: 'change',
        name: t('labelQuota24hChange'),
        sortable: true,
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const hasValue = Number.isFinite(value)
            const isPositive = value > 0
            const sign = isPositive ? '+' : ''
            const renderValue = hasValue ? `${sign}${value.toFixed(2)}%` : '--%'
            return (
                <div className="rdg-cell-value">
                    <QuoteTableChangedCell value={value}>
                        {renderValue}
                    </QuoteTableChangedCell>
                </div>
            )
        },
    },
    {
        key: 'high',
        name: t('labelQuota24hHigh'),
        sortable: true,
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const hasValue = Number.isFinite(value)
            const renderValue = hasValue ? value.toFixed(2) : '--'
            return (
                <div className="rdg-cell-value">
                    <span>{renderValue}</span>
                </div>
            )
        },
    },
    {
        key: 'low',
        name: t('labelQuota24hLow'),
        sortable: true,
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const hasValue = Number.isFinite(value)
            const renderValue = hasValue ? value.toFixed(2) : '--'
            return (
                <div className="rdg-cell-value">
                    <span>{renderValue}</span>
                </div>
            )
        },
    },
    {
        key: 'volume',
        name: t('labelQuota24Volume'),
        sortable: true,
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const hasValue = Number.isFinite(value)
            const renderValue = hasValue
                ? `${(value / 1000000).toFixed(2)} M`
                : '--'
            return (
                <div className="rdg-cell-value">
                    <span>{renderValue}</span>
                </div>
            )
        },
    },
    {
        key: 'trade',
        name: '',
        formatter: ({row}) => {
            const {first, last} = row['pair']
            const tradePair = `${first}-${last}`
            return (
                <div className="rdg-cell-value">
                    <Button variant="outlined" onClick={() => history.push({
                        pathname: `/trading/lite/${tradePair}`
                    })}>Trade</Button>
                </div>
            )
        }
    }
]

export interface QuoteTableProps {
    rawData: QuoteTableRawDataItem[],
    onVisibleRowsChange?: (visibleRows: VisibleDataItem[]) => void
}

export type VisibleDataItem = {
    first: string;
    last: string;
}

export const QuoteTable = withTranslation('tables')(withRouter((props: QuoteTableProps & WithTranslation & RouteComponentProps) => {
    const { t, onVisibleRowsChange, rawData, history } = props
    const formattedRawData = rawData && Array.isArray(rawData) ? rawData : []
    const getScrollIndex = useCallback((e) => {
        const startIndex =  parseInt(String(e.target.scrollTop / 44))
        const data = rawData && Array.isArray(rawData) ? rawData : []
        const viewportRows = data.slice(startIndex, startIndex + 10).map(o => ({
            first: o.pair.first,
            last: o.pair.last
        }))
        if (onVisibleRowsChange) {
            onVisibleRowsChange(viewportRows)
        }
    }, [onVisibleRowsChange, rawData])

    const finalData = formattedRawData.map(o => Object.values(o))
    const defaultArgs: any = {
        rawData: [],
        columnMode: getColumnModelQuoteTable(t, history),
        generateRows,
        generateColumns,
    }

    return (
        <TableStyled>
            <Table {...{...defaultArgs, ...props, rawData: finalData}} onScroll={getScrollIndex} />
        </TableStyled>
    )
}))
